html,
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  background-color: #f4f4f4;
  color: #3b3b3b;
  font-weight: 400;
  line-height: 1.25em;
}
@media screen and (max-width: 800px) {
  html,
  body {
    font-size: 19px;
  }
}
@media screen and (min-width: 800px) {
  html,
  body {
    font-size: 17px;
  }
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
div,
html,
body {
  margin: 0;
  padding: 0;
}
strong,
em {
  font-weight: 700;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  font-weight: 400;
  line-height: 1.2em;
}
h1 {
  margin: 2em 0 0.3em;
}
@media screen and (max-width: 800px) {
  h1 {
    font-size: 1.5em;
  }
}
@media screen and (min-width: 800px) {
  h1 {
    font-size: 2em;
  }
}
h1:first-of-type {
  margin-top: 0;
}
h2 {
  font-size: 1.5em;
  margin: 1.3em 0 0.3em;
}
h2.title:first-of-type {
  margin-top: 0;
}
h3,
h4,
h5,
h6 {
  font-size: 1.2em;
  margin: 1.5em 0 0.5em;
}
a {
  font-weight: 700;
}
a,
a:visited,
a:link {
  color: #7e00ff;
  text-decoration: none;
}
a:active,
a:hover {
  color: #4c0099;
  text-decoration: none;
}
a::selection,
a::-moz-selection {
  color: #7e00ff;
}
::selection,
::-moz-selection {
  background-color: #e5ccff;
}
.container {
  width: 100%;
  max-width: 800px;
}
.cover {
  width: 100%;
  height: auto;
  max-width: 400px;
  max-height: 400px;
}
